.raw{
    padding-left: 20px;
    color: white;
}
.posters{
    display:flex;
    padding: 20px;
    overflow-x:scroll;
    overflow-y:hidden;
}
.posters::-webkit-scrollbar{
    display:none;
}
.poster{
    cursor: pointer;
    max-height: 250px;
    margin-right: 10px;
}
.smallposter{
    cursor: pointer;
    max-height: 150px;
    margin-right: 10px;
}
.poster:hover{
    transform: scale(1.2);
}
@media screen and (max-width:800px){
   .raw{
       padding-left: 15px;
   }
}
@media screen and (max-width:800px){
    .poster{
        padding: 5px;
    }
}